import { useEffect, useState } from "react";

export default function useISOSstring(isos: string) {
  const [date, setDate] = useState<null | string>(null);

  useEffect(() => {
    const now = new Date();
    const targetDate = new Date(isos);
    const elapsedTime = now.getTime() - targetDate.getTime();

    const seconds = Math.floor(elapsedTime / 1000); //     
    const minutes = Math.floor(seconds / 60); //     
    const hours = Math.floor(minutes / 60); //     
    const days = Math.floor(hours / 24); //     

    if (days > 365) {
      return setDate(`${Math.floor(days / 365)} `);
    }
    if (days > 30) {
      return setDate(`${Math.floor(days / 30)} `);
    }
    if (hours === 0) {
      return setDate("");
    }
    if (hours < 24) {
      return setDate(`${hours} `);
    }
    if (hours > 24) {
      return setDate(`${days} `);
    }
    return setDate(isos);
  }, [isos]);

  return date;
}
